.App {
  text-align: center;
}

.App-header {
  text-align: center;
  background-color: #222;
  padding: 1px;
  color: white;
}

.App-title {
  font-size: 15px;
}

.App-intro {
  font-size: large;
}

.Div-border-black{
  border-style: solid;
  border-width: 2px;
  border-color: black;
}

.Div-border-red{
  border-style: dashed;
  border-width: 2px;
  border-color: red;
}

.Div-border-yellow{
  border-style: dashed;
  border-width: 2px;
  border-color: yellow;
}

.Div-border-blue{
  border-style: dashed;
  border-width: 2px;
  border-color: blue;
}

.Div-border-green{
  border-style: dashed;
  border-width: 2px;
  border-color: green;
}

.Div-side1{
  float:left;
  /*width:35%;*/
  min-width: 500px;
}
.Div-side2{
  float:left;
  /*width:35%;*/
  min-width: 400px;
}
.Div-side3{
  float:left;
  /*width:18%;*/
  min-width: 200px;
}

.Div-scroll{
  height: 350px;
  overflow-y: auto;
}
.Div-bg-color-orangeish{
  background-color: rgb(255, 173, 86);
}
.Div-bg-color-yellowish{
  background-color: rgb(253, 255, 131);
}
.Div-bg-color-greenish{
  background-color: rgb(119, 255, 111);
}

.rejt-name{
  color: chartreuse;
  font-weight: bold;
}

.rejt-collapsed {
  cursor: pointer;
}
.rejt-edit-form{
  padding: 3px;
  margin: 10px;
  background-color: white;
}
.rejt-value{
  padding: 3px;
  margin: 10px;
  color: black;
  background-color: #e7e7e7;
  cursor: pointer;
}
.rejt-value:hover{
  color: rgb(0, 0, 0);
  padding: 3px;
  margin: 10px;
  cursor: pointer;
  background-color: #bebebe;
}


.rejt-minus-menu{
  cursor: pointer;
}

.rejt-plus-menu{
  cursor: pointer;
}

.Font-size15px{
  font-size: 15px;
}

.ModalOverlay {
  position: fixed; 
  top: 0px; 
  left: 0px; 
  right: 0px; 
  bottom: 0px; 
  background-color: rgba(255, 255, 255, 0.35);
  z-index: 2;
}
.Modal {
  top                   : '50%';
  left                  : '50%';
  right                 : 'auto';
  bottom                : 'auto';
  margin-Right           : '-50%';
  -webkit-transform             : 'translate(-50%, -50%)';
      -ms-transform             : 'translate(-50%, -50%)';
          transform             : 'translate(-50%, -50%)';
  background-color: rgba(255, 255, 255, 0.679);
}
.react-resizable {
  position: relative;
}
.react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  background: url('data:image/svg+xml;base64,');
  background-position: bottom right;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: se-resize;
}

.floating-btn{
  position: fixed;
  bottom: 10px;
  right: 10px; 
  z-index: 1;
}

.relative{
  position: relative;
}

.container {
  display: -ms-flexbox;
  display: flex; /* or inline-flex */
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.item {
  margin: auto;
  overflow-y: scroll;
  height: 45vh;
  -ms-flex: 0 1 content;
      flex: 0 1 content;
  /*resize: both;*/
  border: #c6c6c6 5px solid;
  border-radius: 5px;
  /*width: 200px;*/
}
.flexgrowbig{
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
}
.content{
  width: 100%;
  border: #c6c6c6 5px solid;
  border-radius: 5px;
  overflow-y: scroll;
}
h3{
  margin: 0;
}


